import React from 'react'
import '../css/App.css'
import linha from '../assets/Line 2.png'
import goGreen from '../assets/Go Green Recycling 1.png'
import notebook from '../assets/MacBook Pro 16.png'
import iphone from '../assets/iPhone 12 Pro.png'
import Nav from './Nav'
import Rodape from './Rodape'
import { Link } from 'react-router-dom'


function Home (){
    return <main>
      <Nav />
  <div class="text-one">
    <div class='texto-um'>
      <p id='coleta'> Coleta Sustentvel </p>
      <p>Nosso objetivo  que voc 
      encontre um ponto de 
      coleta <br></br>de algum material 
       especfico pertinho de voc,
      e ainda <br></br> ajude outras 
      pessoas caso voc encontre 
      um ponto de <br></br>coleta diferente.</p>
    </div>
    <div class='imagem-notebook'>
      <img src={notebook} alt='' id='notebook-image' ></img>
  </div>
  </div>




  <div class="text-two">
    <h2 id='h2-text-two'> No existe planeta b, vamos nos unir?</h2>
    <Link to='/login' style={{ textDecoration: 'none', color: 'white', margin: '0 auto'}}> <button class="start-button">Comear</button></Link>
  </div>

  <div class="text-three">

    <div class='imagem-tres-div'>
      <p><img src={goGreen} alt='logo go Green' id='image-goGreen' /></p>
    </div>

    <div class='texto-tres'>
      <h3>Voc sabia?</h3>
      <p id='p-texto-tres'>O Brasil  o 4 pas que mais produz lixo no mundo</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
      <p id='p-texto-tres'>So Paulo  o estado que mais produz lixo no pas.</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
      <p id='p-texto-tres'>A populao brasileira gera, por ano, 1.5 milhes <br></br> de tonelada de lixo eletrnico</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
      <p id='p-texto-tres'>Uma pesquisa aponta que mais de 80% dos
      municpios <br></br> brasileiros no tratam de forma
      adequada <br></br> o entulho gerado pela 
      construo civil.</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
  </div>

  </div>

  <div class="text-four">

  <div class='texto-tres'>
      <p id='p-texto-tres'>1 litro de leo pode contaminar at 25 mil litros de gua</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
      <p id='p-texto-tres'>Cada quilograma de medicamento descartado incorretamente <br></br> pode acabar contaminando at 450 mil litros de gua, causando <br></br>
      um grave problema ambiental <br></br>
      e de sade pblica.</p>
      <img src={linha} alt='imagem de linha' id='imagem-linha' width={430} />
  </div>

    <div class='imagem-four-div'>
    <img src={iphone} alt='' id='iphone' ></img>
    </div>
  </div>
  <Rodape />
</main>
}

export default Home